/******************************************
 *        components/toggle-block
 =========================================*/
.toggle-block--title {
  line-height: 1.25rem;
  cursor: pointer;
  color: #5772ac; }

.toggle-block--btn {
  display: inline-block;
  height: 1.25rem;
  width: 1.25rem;
  float: right;
  cursor: pointer;
  border: 1px solid lightgray;
  border-radius: 1.25rem;
  background-image: url(data:image/svg+xml;base64,);
  background-repeat: no-repeat;
  background-size: 70% auto;
  background-position: center; }
  .open .toggle-block--btn {
    transform: scaleY(-1); }

.toggle-block--content.hide {
  display: none;
  overflow: hidden; }
